import React, { Component } from "react";
import L from "leaflet";
import * as ELG from "esri-leaflet-geocoder";
import { Map, TileLayer, CircleMarker, Popup,Circle } from "react-leaflet";
import "./Maps.css";
import axios from 'axios'
// import marker icons
import { NotificationContainer, NotificationManager } from 'react-notifications';
import Header from '../../components/Headers/Header';
import createHistory from 'history/createBrowserHistory'

delete L.Icon.Default.prototype._getIconUrl;


L.Icon.Default.mergeOptions({
  iconRetinaUrl:
    "https://unpkg.com/leaflet@1.4.0/dist/images/marker-icon-2x.png",
  iconUrl: "https://unpkg.com/leaflet@1.4.0/dist/images/marker-icon.png",
  shadowUrl: "https://unpkg.com/leaflet@1.4.0/dist/images/marker-shadow.png"
});


const history = createHistory()


class MapComp extends Component {



  constructor(props) {
    super(props);
    this.state = { machine: [] };
  }

  componentDidMount() {


    axios.get(`https://vast-waters-25529.herokuapp.com/api/agencyCollection/getAllMachine/`, {

    })
      .then(res => {
        console.log(res.data);

        this.setState({ machine: res.data })

        console.log(this.state.machine)

      })
      .catch(err => {

        console.log(err)
      })


    const map = this.leafletMap.leafletElement;
    const searchControl = new ELG.Geosearch().addTo(map);
    const results = new L.LayerGroup().addTo(map);

    searchControl.on("results", function (data) {
      results.clearLayers();
      for (let i = data.results.length - 1; i >= 0; i--) {
        results.addLayer(L.marker(data.results[i].latlng));
        //console.log(data.results[i].text)
        //console.log(data.results[i].latlng)

        axios.post('https://vast-waters-25529.herokuapp.com/api/agencyCollection/addMachine',

          {
            location: data.results[i].text, latitude: data.results[i].latlng.lng,
            longitude: data.results[i].latlng.lat, color: 'green'
          }

        ).then((res) => {
          console.log(res.data.data.location);


          NotificationManager.success(`Success add location ${res.data.data.location}`);  //render 

          axios.patch('https://vast-waters-25529.herokuapp.com/api/agencyCollection/addTrashToMachine/' + res.data.data._id, {
            location: res.data.data.location, state: 'Empty', quantity: 0

          })
            .then((res) => {
              console.log(res);

              setTimeout(() => {

                history.go(0)

              }, 2000);

            }).catch((err) => console.log(err))

        }).catch((err) => console.log(err))

      }
    });




  }

  render() {


    const center = [36.866537, 10.164723];
    return (
      <>

        <Header />
        <Map
          style={{ height: "80vh", width: '95%', marginLeft: '3%', marginTop: '2%' }}
          center={center}
          zoom="6.5"
          ref={m => {
            console.log(m)
            this.leafletMap = m;
          }}
          className='maps'
        >
          <TileLayer
            attribution="&copy; <a href='https://osm.org/copyright'>OpenStreetMap</a> contributors"
            url={"http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"}
          />

          <CircleMarker center={[36.066537, 10.164723]} color='cyan'  fillColor="magenta"
          radius={280} ></CircleMarker>

            {
              console.log(this.state.machine)
            }

            {this.state.machine != null &&

              this.state.machine.map((e, i) => {

                return (

                  <CircleMarker key={i} center={[e.longitude, e.latitude]}
                    color={e.color} radius={20}>

                    <Popup >



                      {e.color === 'red' ?

                        <text>Location : <text style={{ color: 'red' }}>{e.location}</text></text>
                        : <text>Location : <text style={{ color: 'green' }}>{e.location}</text></text>
                      } <br />

                      <text>Longitude : {e.longitude}</text><br />
                      <text>Latitude : {e.latitude}</text><br />

                      <text>Trash quantity : {e.trash[0].quantity}% </text> <br />
                      <text>Trash state : {e.trash[0].state}</text> <br /><br />

                    </Popup>

                  </CircleMarker>
                )
              })
            }
          <div className="pointer" />
        </Map>

        <span id='demo'></span>

        <NotificationContainer />
      </>
    );
  }
}

export default MapComp;
