import React from 'react';

import {
 
  Table,
 
} from "reactstrap";


import 'react-toastify/dist/ReactToastify.css';

const Posts = ({posts}) => {


    return (
        <Table className="align-items-center table-flush" responsive>
            <thead className="thead-light">
                <tr>
                    <th scope="col">Location</th>
                    <th scope="col">Reservation name</th>
                    <th scope="col">Trash state</th>
                    <th scope="col">Trash quantity</th>
                    <th scope="col">Status</th>
                </tr>
            </thead>

            {posts &&
                posts.map((e, i) => {

                        return (
                            <tbody>
                                <tr>
                                    <th scope="row">{e.location}</th>
                                    {e.color === 'red' ?
                                        <p style={{ marginTop: '11%', marginLeft: '20%' }}>{e.reservation[0].name}</p>

                                        : <p style={{ marginTop: '11%', marginLeft: '20%', color: 'green' }}>Nothing</p>

                                    }
                                    {e.trash &&

                                        <>
                                            <td>{e.trash[0].state}</td>
                                            <td >
                                                <p style={{ marginLeft: '30%' }}>{e.trash[0].quantity}%</p>
                                            </td>
                                            {e.color === 'red' ?
                                                (<td> <p style={{ color: e.color }}>
                                                    <img src="https://img.icons8.com/fluency/48/000000/remove-bookmark.png"
                                                        style={{ width: '30px' }} /></p> </td>)
                                                : (<td> <p style={{ color: e.color }}>
                                                    <img src="https://img.icons8.com/color/48/000000/check-all--v1.png"
                                                        style={{ width: '30px' }} /></p> </td>)}
                                        </>

                                    }

                                </tr>

                            </tbody>

                        )
                    
                })}

        </Table>

    );
};

export default Posts;