import React, { useState, useEffect } from 'react';
import {
  Badge,
  Card,
  CardHeader,
  CardFooter,
  DropdownMenu,
  DropdownItem,
  UncontrolledDropdown,
  DropdownToggle,
  Media,
  Pagination,
  PaginationItem,
  PaginationLink,
  Progress,
  Table,
  Container,
  Row,
  UncontrolledTooltip,
} from "reactstrap";
// core components
import Header from "components/Headers/Header.js";
import axios from 'axios'


const Tables = ({ history }) => {

  const [users, setUsers] = useState();


  useEffect(async () => {

    await axios.get('https://vast-waters-25529.herokuapp.com/api/agencyCollection/getAllUsers'
    ).then((res) => {

      console.log(res.data);
      setUsers(res.data);

      console.log('users', users);


    }).catch(err => {
      console.log(err)
    })

  }, [setUsers, users]);

  const handelSubmit = async (e, id) => {

    e.preventDefault();

    console.log(id)

    await axios.delete('https://vast-waters-25529.herokuapp.com/api/agencyCollection/deleteagencyCollection/' + id)
      .then(res => {

        console.log(res);
        history.go(0);
      }).catch((err) => {

        console.log(err)

      })


  }



  return (
    <>
      <Header />
      <Container className="mt--7" fluid>
        <Row>
          <div className="col">
            <Card className="shadow">
              <CardHeader className="border-0">
                <h3 className="mb-0">Collecting Agency</h3>
              </CardHeader>
              <Table className="align-items-center table-flush" responsive>
                <thead className="thead-light">
                  <tr>
                    <th scope="col">CollectingAgency name</th>
                    <th scope="col">Address</th>
                    <th scope="col">PhoneNumber</th>
                    <th scope="col">Logo</th>
                    <th scope="col">Status</th>
                    <th scope="col" />
                  </tr>
                </thead>


                <tbody>



                  {users && users.map((e, i) => {


                    return (

                      <tr>
                        <th scope="row">
                          <Media className="align-items-center">
                            <a
                              className="avatar rounded-circle mr-3"
                              href="#pablo"
                              onClick={(e) => e.preventDefault()}
                            >
                              <img
                                alt="..."
                                src={e.logo}
                              />
                            </a>
                            <Media>
                              <span className="mb-0 text-sm">
                                {e.name}
                              </span>
                            </Media>
                          </Media>
                        </th>

                        <td>{e.address}</td>
                        <td>
                          <Badge color="" className="badge-dot mr-4">
                            <i className="bg-warning" />
                            {e.phoneNumber}
                          </Badge>
                        </td>
                        <td>
                          <div className="avatar-group">
                            <a
                              className="avatar avatar-sm"
                              href="#pablo"
                              id="tooltip742438047"
                              onClick={(e) => e.preventDefault()}
                            >
                              <img
                                alt="..."
                                className="rounded-circle"
                                src={e.logo}
                              />
                            </a>
                            <UncontrolledTooltip
                              delay={0}
                              target="tooltip742438047"
                            >
                              {e.email}
                            </UncontrolledTooltip>

                          </div>
                        </td>
                        {/*bouton active ou inactive*/}

                        {users && users.map((e, i) => {

                          return (

                            <td>
                              <div className="d-flex align-items-center">
                                <span className="mr-2">{e.isconnected}</span>
                                <div>
                                  <Progress
                                    max="100"
                                    value="100"
                                    barClassName={e.colorIsConnected}
                                  />
                                </div>
                              </div>
                            </td>


                          )
                        })}
                        <td className="text-right">
                          <UncontrolledDropdown>
                            <DropdownToggle
                              className="btn-icon-only text-light"
                              href="#pablo"
                              role="button"
                              size="sm"
                              color=""
                              onClick={(e) => e.preventDefault()}
                            >
                              <i className="fas fa-ellipsis-v" />
                            </DropdownToggle>
                            <DropdownMenu className="dropdown-menu-arrow" right>
                              <DropdownItem
                                href="#pablo"
                                onClick={(e) => e.preventDefault()}
                              >
                                Update
                              </DropdownItem>
                              <DropdownItem
                                href="#pablo"
                                onClick={(k) => handelSubmit(k, e._id)}
                              >
                                Delete
                              </DropdownItem>

                            </DropdownMenu>
                          </UncontrolledDropdown>
                        </td>


                      </tr>
                    )

                  })}

                </tbody>
              </Table>

              <CardFooter className="py-4">
                <nav aria-label="...">
                  <Pagination
                    className="pagination justify-content-end mb-0"
                    listClassName="justify-content-end mb-0"
                  >
                    <PaginationItem className="disabled">
                      <PaginationLink
                        href="#pablo"
                        onClick={(e) => e.preventDefault()}
                        tabIndex="-1"
                      >
                        <i className="fas fa-angle-left" />
                        <span className="sr-only">Previous</span>
                      </PaginationLink>
                    </PaginationItem>
                    <PaginationItem className="active">
                      <PaginationLink
                        href="#pablo"
                        onClick={(e) => e.preventDefault()}
                      >
                        1
                      </PaginationLink>
                    </PaginationItem>
                    <PaginationItem>
                      <PaginationLink
                        href="#pablo"
                        onClick={(e) => e.preventDefault()}
                      >
                        2 <span className="sr-only">(current)</span>
                      </PaginationLink>
                    </PaginationItem>
                    <PaginationItem>
                      <PaginationLink
                        href="#pablo"
                        onClick={(e) => e.preventDefault()}
                      >
                        3
                      </PaginationLink>
                    </PaginationItem>
                    <PaginationItem>
                      <PaginationLink
                        href="#pablo"
                        onClick={(e) => e.preventDefault()}
                      >
                        <i className="fas fa-angle-right" />
                        <span className="sr-only">Next</span>
                      </PaginationLink>
                    </PaginationItem>
                  </Pagination>
                </nav>
              </CardFooter>
            </Card>
          </div>
        </Row>
        {/* Dark table */}

      </Container>
    </>
  );
};

export default Tables;
